/** @jsx jsx */
/*eslint no-unused-vars: 0*/
import React from 'react';
import { jsx } from 'theme-ui';
import { Flex, Box, Heading } from 'rebass';
import TabCollectionBlock from './TabCollectionBlock';
import strings from './strings.json';
import Tabs from 'react-bootstrap/Tabs';
import Tab from 'react-bootstrap/Tab';

const { ariaBestSellers } = strings;

const MainPageTabs = (props) => {
  const { tabs, data } = props;
  return (
    <Box width={1} bg={'backgroundGrey'} sx={{ position: 'relative' }}>
      <Heading fontSize={['8vw', '4vw', '2.5vw']} color={'primary'} sx={{ fontWeight: '400', textAlign: 'center', textTransform: 'capitalize' }}>{ariaBestSellers}</Heading>
      <Flex flexDirection={[ 'column', 'row', 'row']}>
        <Tabs defaultActiveKey="face">
          {tabs.children.map((block, index) => {
            if (block.type === 'collection' && data.tabs.nodes.filter(collection => collection.handle === block.handle )[0] ) {
              return (
                <Tab key={index} eventKey={block.handle} title={block.handle}>
                  <TabCollectionBlock
                    collection={
                      data.tabs.nodes.filter(
                        collection => collection.handle === block.handle
                      )[0]
                    }
                  />
                </Tab>
              );
            } else {
              return '';
            }
          })}
        </Tabs>
      </Flex>
    </Box>
  );
};

export default MainPageTabs;
