/** @jsx jsx */
/*eslint no-unused-vars: 0*/
import React from 'react';
import GatsbyLink from 'gatsby-link';
import { jsx } from 'theme-ui';
import styled from '@emotion/styled/macro';
import { Flex, Box, Text, Heading } from 'rebass';
import NoImage from '../../components/Icons/NoImage';

import substrDescription from '../../utils/substrDescription';

const CardWrapper = styled(Box)`
  & img {
    position: absolute;
    top: 0px;
    left: 0px;
    width: 100%;
    height: 100%;
    padding: 2vw;
    object-fit: contain;
    object-position: center center;
    opacity: 1;
    transition-property: all;
    transition-duration: 0.6s;
    transition-timing-function: ease-in-out;
    transition-delay: 0s;
  }

  &:hover img {
    transform: scale(1.05);
    transition-property: all;
    transition-duration: 0.6s;
    transition-timing-function: ease-in-out;
    transition-delay: 0s;
  }
`;

const TabCollectionBlock = props => {
  const {
    handle,
    title,
    products,
  } = props.collection;

  return (
    <Box width={1}>
      <Flex width={1} p={['3vw']}>
        {products ? products.map((product, index) => {
          if (index <= 3) {
            return (
              <Box width={[1, 1 / 3, 1 / 4]} m={['0 1vw']} key={index} sx={{ overflow: 'hidden' }}>
                <GatsbyLink to={product.fields.shopifyThemePath}>
                  <CardWrapper p={['14px', '25px']} bg={'background'} sx={{ borderWidth: '1px', borderStyle: 'solid', borderColor: 'grey' }}>
                    {product.fields.firstImage ? (
                      <Box sx={{ position: 'relative' }}>
                        <img src={product.fields.firstImage.originalSrc} alt={product.handle}  />
                        <Box sx={{width: '100%', pb: '100%' }}></Box>
                      </Box>
                    ) : (
                      <NoImage color="grey" p={4} mb={4} sx={{ borderWidth: '1px', borderStyle: 'solid', borderColor: 'grey' }}/>
                    )}
                    {product.title ? (
                      <Text sx={{ overflow: "hidden", height: "60px", marginBottom: "10px", fontSize: "14px", fontWeight: "300", textAlign: "center", lineHeight: "1.43" }}>{substrDescription(product.title, 60)}</Text>
                    ) : (
                      <Text mb={4} sx={{ fontSize: "14px", fontWeight: "300", textAlign: "center", lineHeight: "1.43" }}>Product title</Text>
                    )}
                    {product.priceRange ? (
                      <Text sx={{ color: 'primary', fontWeight: 'bold', marginBottom: "5px", fontSize: "14px", fontWeight: "300", textAlign: "center", }}>{product.priceRange.minVariantPrice.amount}</Text>
                    ) : (
                      <Text sx={{ color: 'primary', fontWeight: 'bold', marginBottom: "5px", fontSize: "14px", fontWeight: "300", textAlign: "center", }}>99.99</Text>
                    )}
                  </CardWrapper> 
                </GatsbyLink>
              </Box>
            );
          } else {

          };
        })
        : <NoImage color="grey" p={4} mb={4} sx={{ borderWidth: '1px', borderStyle: 'solid', borderColor: 'grey' }}/>}
      </Flex>
    </Box>
  );
};

export default TabCollectionBlock;
